@import url('https://fonts.googleapis.com/css2?family=Amaranth&family=Lilita+One&family=Sarabun&family=ZCOOL+XiaoWei&display=swap');
/*
font-family: 'Amaranth', sans-serif;
font-family: 'ZCOOL XiaoWei', serif;
font-family: 'Lilita One', cursive;
font-family: 'Sarabun', sans-serif;
*/

.app {
    background-image: url('./assets/cold-bg.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.5s ease-out;
    color: white;
}

.app.warm {
  background-image: url('./assets/warm-bg.jpg');
}

main {
  height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
}

.search-box {
  margin: 0 auto;
  width: 100%;
}

.search-box .search-bar {
  display: block;
  margin: 0 auto 70px auto;  
  width: 95%;
  padding: 15px;

  appearance: none;
  outline: none;
  border: none;
  border-radius: 0px 0px 16px 16px;

  background: rgba(255, 255, 255, 0.5);
  transition: 0.4s ease;
  box-shadow: 0px 3px rgba(0, 0, 0, 0.2);
  
  color: #313131;
  font-family: 'Sarabun', sans-serif;
  font-size: 20px;
}

.search-box .search-bar:focus {
  background: rgba(255, 255, 255, 0.75);
}

.location-box .location{
  font-size: 32px;
  font-family: 'Lilita One', cursive;
  text-align: center;
  text-shadow: 3px 3px rgb(50, 50, 50, 0.5);
}

.location-box .date {
  font-size: 20px;
  font-family: 'Amaranth', sans-serif;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  margin: 30px auto;
  padding: 15px 25px;
  display: inline-block;

  font-size: 102px;
  font-family: 'ZCOOL XiaoWei', serif;
  font-weight: 900;

  text-shadow: 2px 10px rgba(0, 0, 0, 0.6);
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 2px 4px rgba(0, 0, 0, 0.2);
}

@media(max-width:800px){
  .weather-box .temp {
    font-size: 90px;
  }
}

@media (min-width: 800px){
  .app {
    background-image: url('./assets/bg.jpg');
  }

  .search-box .search-bar {
    width: 400px;
  }
}

.weather-box .weather {
  font-size: 32px;
  font-family: 'ZCOOL XiaoWei', serif;
  font-weight: 700;
  margin-bottom: 15px;
  text-shadow: 0.5px 4px rgba(0, 0, 0, 0.4);
}

#love {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  bottom: 0;
}

#love > span {
  font-family: 'ZCOOL XiaoWei', serif;
  font-weight: 400;
  font-size: 12px;
  color: white
}

#love > img {
  width: 40px;
  padding: 10px;
}

#love > span > a {
  text-decoration: none;
  color: yellow;
}